// Tip 实时库存分析
import request from '@/apis/request.wms'

// 主列表
export function getListApi(data = {}) {
  return request({
    url: '/statistics/checkWarehouseResult/pageList',
    method: 'post',
    data
  })
}

// 打印
export function printApi(data = {}) {
  return request({
    url: '/statistics/checkWarehouseResult/print',
    method: 'post',
    data
  })
}

