<template>
  <div class="app-container">
    <!-- 头部搜索 -->
    <div class="clearfix top-filter">
      <el-form ref="filterItem" :model="query" inline @submit.native.prevent>
        <el-form-item prop="code">
          <el-input v-model="query.code" class="filter-item" filterable clearable placeholder="单号" style="width: 178px;" />
        </el-form-item>
        <el-form-item prop="startTime">
          <el-date-picker
            v-model="query.startTime"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="生成单据开始时间"
            @change="query.endTime = ''"
          />
        </el-form-item>
        <el-form-item prop="endTime">
          <el-date-picker
            v-model="query.endTime"
            class="filter-item"
            type="date"
            style="width: 178px;"
            value-format="yyyy-MM-dd"
            placeholder="生成单据结束时间"
            :picker-options="{disabledDate: (time) => time.getTime() < new Date(query.startTime).getTime()}"
          />
        </el-form-item>
        <el-button
          class="filter-item"
          type="success"
          icon="el-icon-search"
          @click="init"
        >搜索</el-button>
        <el-button
          class="filter-item"
          type="warning"
          icon="el-icon-refresh-left"
          @click="resetForm('filterItem')"
        >重置</el-button>
      </el-form>
    </div>
    <el-row>
      <el-button
        class="filter-item"
        type="primary"
        icon="el-icon-plus"
        @click="handleExport"
      >导出</el-button>
    </el-row>
    <el-row>
      <table-com
        :ref="refTableName"
        :key="baseTableKey"
        :ref-name="refName"
        :loading="loading"
        :selection="false"
        :table-column-sort="true"
        :form-ident="formIdent"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="120"
        :refresh-key="refreshKey"
        :expand-list="expandList"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" @click="handleDetail(scope.row)">查看详情</el-button>
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" :page-sizes="[50, 100, 150, 500]" @pagination="init" />
  </div>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
import { getListApi, printApi } from '@/apis/modules/analysis/checkWarehouseResult'
import tableHeadSet from '@/mixins/tableHeadSet'
import { mapGetters } from 'vuex'
import { _debounce } from '@/utils'
import * as XLSX from 'xlsx'
import { saveAs } from 'file-saver'

export default {
  // name: 'AnalysisCheckWarehouseResult',
  components: {
  },
  mixins: [paginationPage, tableHeadSet],
  provide() {
    return
  },
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      query: {
        code: '',
        startTime: '',
        endTime: ''
      },
      columnSets: [],
      refTableName: 'baseTableRef',
      refName: 'table',
      baseTableKey: 1
    }
  },
  computed: {
    ...mapGetters('app', {
      navbarWarehouseCheckedList: 'navbarWarehouseCheckedList'
    })
  },
  mounted() {
    this.init()
  },
  methods: {
    init(type) {
      this.loading = true
      const params = {
        ...this.query,
        warehouseIds: this.navbarWarehouseCheckedList.map(item => item.id)
      }

      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.baseTableKey++
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '操作') {
        return false
      }
    },
    handleDetail({ id }) {
      printApi({ id })
        .then(res => {
          const { code, data } = res
          if (!code) {
            window.open(`${process.env.VUE_APP_BASE_API_WMS}${data}`)
          }
        })
        .catch(err => {
          console.log('err', err)
        })
    },
    handleExport: _debounce(function() {
      const elTable = this.$refs[this.refTableName].$refs[this.refName]
      // 因为element-ui的表格的fixed属性导致多出一个table，会下载重复内容，这里删除掉
      if (elTable.$el.querySelector('.el-table__fixed')) {
        elTable.$el.removeChild(elTable.$el.querySelector('.el-table__fixed'))
      }
      if (elTable.$el.querySelector('.el-table__fixed-right')) {
        elTable.$el.removeChild(elTable.$el.querySelector('.el-table__fixed-right'))
      }
      const wb = XLSX.utils.table_to_book(elTable.$el)
      const wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
      try {
        // 导出Excel
        const time = new Date().toLocaleString().replace(/\//g, '').replace(/:/g, '').replace(' ', '')
        saveAs(new Blob([wbout], { type: 'application/octet-stream' }), `盘点结存单_${time}.xlsx`)
        this.baseTableKey++ // 更新表格组件，防止操作列消失
      } catch (e) {
        if (typeof console !== 'undefined') console.error(e, wbout)
      }
      return wbout
    }, 800, false),
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>

